const debounce = (func, delay) => {
  let task = null;
  return function () {
    if (task) {
      clearTimeout(task)
    }
    task = setTimeout(() => {
      func.apply(this, arguments)
    }, delay)
  }
};

const throttle = (func, delay) => {
  let task = null;
  return function () {
    if (!task) {
      task = setTimeout (() => {
        task = null;
        func.apply(this, arguments)
      }, delay)
    }
  }
};

export default {debounce, throttle}



