          this.backToUndiagnosed()
        }
      },
      /*点击添加描述*/
      add_description_l(){
        let dom1 = this.$refs.descInput;
        dom1.focus();
        this.leftEyeDesc = true;
        this.rightEyeDesc = false;
        // setTimeout(() => {
        //   dom1.focus()
