      add_description_r(){
        let dom1 = this.$refs.descInput;
        dom1.focus();
        this.leftEyeDesc = false;
        this.rightEyeDesc = true;
      },

      /*医生选择照片后，诊断界面需要展示医生选择后的照片*/
      data_process(msg){
        if(msg['left_src']){
          this.left_src = msg['left_src'];
        }
        if(msg['right_src']){
          this.right_src = msg['right_src'];
        }
      },

      /*点击照片后展示所有照片*/
      displayAllPictures(){
        this.$displayPicture(true);
        let pic_dict = {};
        pic_dict['right_srcList'] = this.right_srcList;
        pic_dict['left_srcList'] = this.left_srcList;
        this.bus.$emit('displayAllPictures', pic_dict);
