      /*关闭右眼诊断标签*/
      handleCloseR(tag) {
        this.dynamicTagsR.splice(this.dynamicTagsR.indexOf(tag), 1);
      },

      /*关闭右眼描述标签*/
      handleCloseDR(tag) {
        this.descriptionTagsR.splice(this.descriptionTagsR.indexOf(tag), 1);
      },

      /*显示添加的输入框*/
      showInputR() {
        this.activeColor2 = 'lightblue';
        this.inputVisibleR = true;
        this.rightEye = true;
        this.leftEye = false;
        this.rightEyeDesc = false;
        this.leftEyeDesc = false;
        this.$nextTick(_ => {
          this.$refs.saveTagInputR.$refs.input.focus();
        });
      },
      /*诊断输入*/
      handleInputConfirmR() {
        this.activeColor2 = 'azure';
        let inputValueR = this.inputValueR;
        if (inputValueR) {
          this.dynamicTagsR.push(inputValueR);
        }
        this.inputVisibleR = false;
        this.inputValueR = '';
        let desLength = this.dynamicTagsR.toString().length;
        if(desLength>40){
          this.$alert('诊断描述不超过40个字符', '提示', {
            confirmButtonText: '确定',
          });
          this.dynamicTagsR.pop();
        }
      },

