      /*关闭建议标签*/
      handleCloseA(tag) {
        this.dynamicTagsA.splice(this.dynamicTagsA.indexOf(tag), 1);
        this.seen = true;
        this.seen2 = false;
        this.value2='';
      },
      /*显示建议输入框*/
      showInputA() {
        this.leftEye=false;
        this.rightEye=false;
        this.activeColor1 = 'lightblue';
        this.inputVisibleA = true;
        this.advice = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputA.$refs.input.focus();
        });
      },
      /*确定输入建议*/
      handleInputConfirmA() {
        this.activeColor1='azure';
        let inputValueA = this.inputValueA;
        let dynamicTagsA = this.dynamicTagsA;
        if (inputValueA) {
          this.dynamicTagsA.push(inputValueA);
        }
        if(dynamicTagsA.length !== 0 ){
          this.seen = false;
        }
        this.inputVisibleA = false;
        this.inputValueA = '';
      },

      /*点击诊断栏处诊断自动添加为诊断的标签*/
      addDiagnose(index){
        if(this.leftEye){
          this.dynamicTagsL.push(index);
            /*去重*/
          this.dynamicTagsL = this.duplicateRemove(this.dynamicTagsL);
          this.handleInputConfirmL();
        }else if(this.rightEye){
          this.dynamicTagsR.push(index);
            /*去重*/
          this.dynamicTagsR = this.duplicateRemove(this.dynamicTagsR);
          this.handleInputConfirmR();
        }
      },


      /*点击诊断栏处诊断自动添加为诊断的标签*/
      addDescription(index){
        if(this.leftEyeDesc){
          this.descriptionTagsL.push(index);
          /*去重*/
          this.descriptionTagsL = this.duplicateRemove(this.descriptionTagsL);
          this.handleInputConfirmDL();

        }else if(this.rightEyeDesc){
          this.descriptionTagsR.push(index);
          /*去重*/
          this.descriptionTagsR = this.duplicateRemove(this.descriptionTagsR);
          this.handleInputConfirmDR();
        }
      },

      /*点击建议栏处建议自动添加为建议的标签*/
      addAdvice(index){
        let dynamicTagsA = this.dynamicTagsA;
        if(this.advice){
          if(dynamicTagsA.length !==0){
            return
          }else{
            this.dynamicTagsA.push(index);
            this.handleInputConfirmA();
          }
          if(index==='定时复查'){
            this.dialogVisible=true;
            // this.seen2=true;
          }
          if(index==='进一步就诊检查'){
            this.uplevelVisible=false;  //如果要打开添加上级医院对话框，将false改为true即可
          }
        }
      },

      formatSex: function (row) {
        return row.patient_gender === "0" ? '男' : row.patient_gender === "1" ? '女' : '未知';
      },


      makeStr:function(){
        let diaStrR = this.dynamicTagsR.toString();  /*将右眼诊断内容由数组形式转化为字符串形式*/
        let diaStrL = this.dynamicTagsL.toString();  /*将左眼诊断内容由数组形式转化为字符串形式*/
        let desStrL = this.descriptionTagsL.toString();
        let desStrR = this.descriptionTagsR.toString();
        if(diaStrR===''){
          diaStrR='无'
        }
        if(diaStrL===''){
          diaStrL='无'
        }
        if(desStrL===''){
          desStrL='无'
        }
        if(desStrR===''){
          desStrR='无'
        }
        this.diaStrR = diaStrR;
        this.diaStrL = diaStrL;
        this.desStrR = desStrR;
        this.desStrL = desStrL;

      },

      /*完成阅片*/
      async finishDiagnose(){
          let res = await this._submitDiagnose();  /*提交诊断内容*/
          /*返回1代表提交诊断成功*/
          if(res === 1){
            //let patient_info = JSON.parse(localStorage.getItem('patient_info'));
            //let para = patient_info['minusMessage'];  //这数据是从消息列表跳转到诊断页面才有的，如果没有则不进行以下请求
            // if(1){
            //   await minusMessageMount(patient_info).then((res) => {
            //     if(res === 1 ){
            //       console.log('消息减去成功')
            //     }
            //     else{
            //       console.log('消息减去失败')
            //     }
            //   });
            // }
            //await get_message_status()
          }else{
            /*已被其他医生诊断和诊断失败则返回false*/
            return false
        }
          this.eyeSeen = true;
          this.diaSeen = false;
          this.finishSeen = true;
          this.beforeSeen = false;
      },

      /*完成阅片并展示加载中*/
      openFullScreen() {
        if(this.dynamicTagsA.length===0){
          this.open(); /*如果没有选择诊断建议，则会出现提示让选择诊断建议*/
        }else{
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.finishDiagnose();
        }, 500);
        }
      },

      /*生成眼底照片*/
      clickCreateImgBtn:function(){
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });

          let patient_id = this.patient_info.patient_id;
          let temp_data = this.getDiagnoseResult();

          let right_origin_src = temp_data['other_data']['right_origin_src'];
          let left_origin_src = temp_data['other_data']['left_origin_src'];
          let username = localStorage.getItem('last_name')+localStorage.getItem('first_name');
          let hospital_name = localStorage.getItem('superior_hospital_name');
          if(!hospital_name){
            hospital_name = localStorage.getItem('hospital_name');
          }
          let data = {};
          data['patient_id'] = patient_id;
          data['right_origin_src'] = right_origin_src;
          data['left_origin_src'] = left_origin_src;
          data['username'] = username;
          data['hospital_name'] = hospital_name;



        createImg(data).then(res=>{
          console.log('res=>',res);
          if(res===0){
            loading.close();
            this.$alert('生成失败', '提示', {
              confirmButtonText: '确定',
            });
          }else{
            loading.close();
            let report1=Math.random().toString(36).substr(2);
            let temWindow=window.open('',report1,'');
            temWindow.document.title="profession";
            temWindow.location=host+res;
          }
        }).catch(error=>{
          loading.close();
          this.$alert('生成失败', '提示', {
            confirmButtonText: '确定',
          });
        })

      },

      /*完成并返回未诊断页面*/
      backToUndiagnosed:function(){
        let page_info = {};
        page_info['page_size'] = this.patient_info.page_size;
        page_info['currentPage'] = this.patient_info.currentPage;
        let result = this.finishDiagnose();
        let page_from = this.$store.state.page_from;
        if(result===false){
          return;
        }
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.$router.push('/'+page_from);
        }, 1000);
      },

      /*仅返回未诊断页面*/
