      /*生成报告*/
      createReport:function(_type){
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        let data = this.getDiagnoseResult();
        data['basic_data']['reportType'] = _type;
        createPdf(data)
          .then(res=>{
            loading.close();
            console.log('res=>',res);
            if(res.length>1){
              let report1=Math.random().toString(36).substr(2);
              //let temWindow=window.open('',report1,'');
              //temWindow.document.title="pdf";
              let patient_name = data['basic_data']['patient_name'];
              let patient_id = data['basic_data']['patient_id'];
              //temWindow.location = host+"media/patient_pdf/"+res+'/'+patient_name+"_"+patient_id+".pdf";
              let pdfSrc = host+"media/patient_pdf/"+res+'/'+patient_name+"_"+patient_id+".pdf";
              //this.$router.push({path:'/previewPdf',query:{'src':pdfSrc}});
              let routerJump = this.$router.resolve({path:'/previewPdf',query:{'src':pdfSrc}});
              // window.open(routerJump.href,'_blank');
              this.openWin(routerJump.href);

            }else{
              this.$alert('诊断报告生成失败', '提示', {
                confirmButtonText: '确定',
              });
            }
          }).catch(error=>{
          loading.close();
          console.log((error));
          this.$alert('请允许弹窗', '提示', {
            confirmButtonText: '确定',
          });
        });
      },

      openWin:function (url) {
        let a = document.createElement("a");
        a.setAttribute("href", url);
        a.setAttribute("target", "_blank");
        a.setAttribute("id", "openWin");
        document.body.appendChild(a);
        a.click();
      },

      /*完成并生成诊断报告*/
      finishAndCreatePdf:function(){
        if(this.dynamicTagsA.length===0){
          this.open(); /*如果没有选择诊断建议，则会出现提示让选择诊断建议*/
        }else{
        try {
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
           setTimeout( async () => {
            loading.close();
            this.createReport(1); /*1代表生成的是非AI报告*/
          }, 500);

        }catch (e) {
          console.log(e)
        }
        }
      },


      /*生成AI报告*/
      createAIreport:function(){
        if(this.dynamicTagsA.length===0){
          this.open(); /*如果没有选择诊断建议，则会出现提示让选择诊断建议*/
        }else{
          try {
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
            setTimeout( async () => {
              loading.close();
              this.createReport(0); /*0代表生成的是AI报告*/
            }, 500);

          }catch (e) {
            console.log(e)
          }
        }
      },

      cancelSelectDate(){
        this.dialogVisible = false;
        this.seen2=false;
        this.value2 = '';
      },

      confirmSelectDate(){
        this.dialogVisible = false;
        this.seen2=true;
        if(!this.value2){
          this.seen2=false;
          this.value2 = '';
        }
      },

      cancelInputHospital(){
        this.uplevelVisible = false;
        this.uplevelHospital = '';
      },

      confirmInputHospital(){
        this.uplevelVisible = false;
      },

      open() {
        this.$alert('请添加建议', '提示', {
          confirmButtonText: '确定',
        });
      },

      replaceDictKey(descriptions){
        let descriptionList = [];
        for(let i=0;i<descriptions.length;i++){
          delete descriptions[i]['id'];
          delete descriptions[i]['times'];
          descriptionList.push(descriptions[i])
        }
        let keyMap = {description: 'value'};
        let result = [];
        for(let i = 0; i<descriptionList.length; i++){
          Object.keys(descriptionList[i]).reduce((newData, key) => {
            let newKey = keyMap[key] || key;
            newData[newKey] = descriptionList[i][key];
            result.push(newData)
          }, {});
        }
        this.restaurants = result;
      },

      querySearchAsync(queryString, cb) {
        let restaurants = this.restaurants;
        let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
        cb(results);
        // clearTimeout(this.timeout);
        // this.timeout = setTimeout(() => {
        //   cb(results);
        // }, 300 * Math.random());
      },
      createStateFilter(queryString) {
        return (state) => {
          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },

      handleSelect(item) {
        if(this.leftEyeDesc){
          if((this.textarea2.length + item.value.length) > 120){
            this.$alert('影像所见不超过120个字符', '提示', {
              confirmButtonText: '确定',
            });
          }else{
            this.textarea2 = this.textarea2 + item.value + '。';
            this.state = '';
          }
        }else{
          if((this.textarea1.length + item.value.length) > 120){
            this.$alert('影像所见不超过120个字符', '提示', {
              confirmButtonText: '确定',
            });
          }else{
            this.textarea1 = this.textarea1 + item.value + '。';
            this.state = '';
          }
        }
      },


      AiDiagnose:function (diagnose) {
        /*pm病理性近视，rm豹纹状眼底*/
        console.log('病理近视',diagnose);
        let left_rm = diagnose['left_rm'];
        let left_pm = diagnose['left_pm'];
        let right_rm = diagnose['right_rm'];
        let right_pm = diagnose['right_pm'];
        let left_oozed_amount = diagnose['left_oozed_amount'];
        let right_oozed_amount = diagnose['right_oozed_amount'];
        let left_oozed_area = diagnose['left_oozed_area'];
        let right_oozed_area = diagnose['right_oozed_area'];
        let left_bleeding_amount = diagnose['left_bleeding_amount'];
        let right_bleeding_amount = diagnose['right_bleeding_amount'];
        let left_bleeding_area = diagnose['left_bleeding_area'];
        let right_bleeding_area = diagnose['right_bleeding_area'];
        let left_max_bleeding_area = diagnose['left_max_bleeding_area'];
        let right_max_bleeding_area = diagnose['right_max_bleeding_area'];
        let left_max_oozed_area = diagnose['left_max_oozed_area'];
        let right_max_oozed_area = diagnose['right_max_oozed_area'];
        let left_prob_dr = diagnose['left_prob_dr'];
        let right_prob_dr = diagnose['right_prob_dr'];
        let left_glaucoma = diagnose['left_glaucoma'];
        let right_glaucoma = diagnose['right_glaucoma'];
        let left_warning = diagnose['left_warning'];
        let right_warning = diagnose['right_warning'];
        let left_rop = diagnose['left_rop'];
        let right_rop = diagnose['right_rop'];

        this.AIdata['iqa_score_left'] = diagnose['left_iqa_score']< 60 ? '不佳' : diagnose['left_iqa_score']>= 75 ? '良好' : '一般';
        this.AIdata['iqa_score_right'] = diagnose['right_iqa_score']< 60 ? '不佳' : diagnose['right_iqa_score']>= 75 ? '良好' : '一般';
        this.AIdata['oozed_area'] = (left_oozed_area*10 + right_oozed_area*10)/10;
        this.AIdata['oozed_amount'] = left_oozed_amount + right_oozed_amount;
        this.AIdata['max_oozed_area'] = (left_max_oozed_area*10 + right_max_oozed_area*10)/10;

        this.AIdata['bleeding_area'] = (left_bleeding_area*10 + right_bleeding_area*10)/10;
        this.AIdata['bleeding_amount'] = left_bleeding_amount + right_bleeding_amount;
        this.AIdata['max_bleeding_area'] = (left_max_bleeding_area*10 + right_max_bleeding_area*10)/10;

        // if(left_warning===1&&right_warning===1){
        //   this.warningShow = true;
        //   this.warning='因左右眼照片质量不达标，故左右眼均不进行AI诊断';
        // }else if(left_warning===1&&right_warning===0){
        //   this.warningShow = true;
        //   this.warning='因左眼照片质量不达标，故左眼不进行AI诊断';
        // }else if(left_warning===0&&right_warning===1){
        //   this.warningShow = true;
        //   this.warning='因右眼照片质量不达标，故右眼不进行AI诊断';
        // }

        /*屏蔽AI辅助诊断*/
        // if(left_rm===1&&left_pm===1){
        //   console.log('左眼是病理性近视');
        //   this.dynamicTagsL.push('病理性近视')
        // }
        // if(left_rm===1){
        //   this.dynamicTagsL.push('豹纹状眼底')
        // }

        // if(right_rm===1&&right_pm===1){
        //   console.log('右眼是病理性近视');
        //   this.dynamicTagsR.push('病理性近视')
        // }
        // if(right_rm===1){
        //   this.dynamicTagsR.push('豹纹状眼底')
        // }
        // if(left_prob_dr===1){
        //   this.dynamicTagsL.push('疑似糖尿病视网膜病变')
        // }
        // if(right_prob_dr===1){
        //   this.dynamicTagsR.push('疑似糖尿病视网膜病变')
        // }
        // if(left_glaucoma===1){
        //   this.dynamicTagsL.push('疑似青光眼')
        // }
        // if(right_glaucoma===1){
        //   this.dynamicTagsR.push('疑似青光眼')
        // }

        if(left_rop===1){
          this.dynamicTagsL.push('疑似ROP')
        }
        if(right_rop===1){
          this.dynamicTagsR.push('疑似ROP')
        }

        if(left_bleeding_area===1){
          this.dynamicTagsL.push('疑似眼底出血')
        }
        if(right_bleeding_area===1){
          this.dynamicTagsR.push('疑似眼底出血')
        }


        if(right_rop===1 || left_rop===1){
          this.dynamicTagsA.push('进一步就诊检查')
        }
        else if(left_bleeding_area===1 || right_bleeding_area===1){
          this.dynamicTagsA.push('进一步就诊检查')
        }

        //   else if(left_rm===1&&left_pm===1){
        //   this.dynamicTagsA.push('进一步就诊检查')
        // }else if(right_rm===1&&right_pm===1){
        //   this.dynamicTagsA.push('进一步就诊检查')
        // }
        this.handleInputConfirmA();
      },

      picture_strengthen:function () {
        let strengthen = this.strengthen;
        let right_src = this.right_src.split('media')[this.right_src.split('media').length-1];
        let left_src = this.left_src.split('media')[this.left_src.split('media').length-1];
        let data = {};
        data['left_src'] = left_src;
        data['right_src'] = right_src;
        if(strengthen){
          const loading = this.$loading({
            lock: true,
            text: '一键增强中',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          pictureStrengthen(data)
            .then(res=>{
              console.log('res=>',res);
              if(res['data']===0){
                loading.close();
                this.$alert('一键增强失败', '提示', {
                  confirmButtonText: '确定',
                });
              }else{
                loading.close();
                if(left_src.search('noimg')===-1){

                  let index= this.left_src.lastIndexOf(".");
                  let left_format = this.left_src.substr(index);

                  this.left_src = this.left_src.replace(left_format,'_strengthen'+left_format);
                  this.left_srcList[0] = this.left_src;
                }
                if(right_src.search('noimg')===-1){
                  let index= this.right_src.lastIndexOf(".");
                  let right_format = this.right_src.substr(index);
                  this.right_src = this.right_src.replace(right_format,'_strengthen'+right_format);
                  this.right_srcList[0] = this.right_src;
                }

              }

            }).catch(error=>{
            console.log((error));
            loading.close();
            this.$alert('一键增强失败', '提示', {
              confirmButtonText: '确定',
            });
          })
        }else{
          if(right_src.search('noimg')===-1){
            this.right_src = this.right_src.replace('_strengthen','');
            this.right_srcList[0] = this.right_src;
          }
          if(left_src.search('noimg')===-1){
            this.left_src = this.left_src.replace('_strengthen','');
            this.left_srcList[0] = this.left_src;
          }
        }

      },

      determine_report_type:function(){
        let type = localStorage.getItem('reportType');  /*0代表为AI报告，车站街道社区医院定制*/
        if(type === '0'){
          this.report_button = false;
          this.AI_report_button = true;
        }else{
          this.report_button = true;
          this.AI_report_button = false
        }

      },

      display_history_diseases:function () {
        let jwsStr = '';
        let historyDisease = this.patient_info['historyDisease'];
        if(historyDisease.length===0){
          // this.jwsDisplayInfo = '无';
          console.log()
        }else{
          this.jwsDisplayInfo = '有';
          console.log('既往史细节',historyDisease[0]);
          if(historyDisease[0]['DM_year']===1){
            jwsStr = '糖尿病 '
          }else if(historyDisease[0]['DM_year']===true){
            jwsStr = '糖尿病 '
          }

          if(historyDisease[0]['HPL_year']===1){
            jwsStr = jwsStr + "高血脂 "
          }else if(historyDisease[0]['HPL_year']===true){
            jwsStr = jwsStr +'高血脂 '
          }

          if(historyDisease[0]['HTN_year']===1){
            jwsStr = jwsStr + "高血压 "
          }else if(historyDisease[0]['HTN_year']===true){
            jwsStr = jwsStr + '高血压 '
          }

          if(historyDisease[0]['left_diopter']){
            if(historyDisease[0]['left_diopter']==='0'){
              jwsStr = jwsStr + "左眼:正视 "
            }else if(historyDisease[0]['left_diopter']==='1'){
              jwsStr = jwsStr + "左眼:低度近视(75~300) "
            }else if(historyDisease[0]['left_diopter']==='2'){
              jwsStr = jwsStr + "左眼:中度近视(300~600) "
            }else if(historyDisease[0]['left_diopter']==='3'){
              jwsStr = jwsStr + "左眼:高度近视(>600) "
            }else if(historyDisease[0]['left_diopter']==='4'){
              jwsStr = jwsStr + "左眼:远视 "
            }
          }

          if(historyDisease[0]['right_diopter']){
            if(historyDisease[0]['right_diopter']==='0'){
              jwsStr = jwsStr + "右眼:正视 "
            }else if(historyDisease[0]['right_diopter']==='1'){
              jwsStr = jwsStr + "右眼:低度近视(75~300) "
            }else if(historyDisease[0]['right_diopter']==='2'){
              jwsStr = jwsStr + "右眼:中度近视(300~600) "
            }else if(historyDisease[0]['right_diopter']==='3'){
              jwsStr = jwsStr + "右眼:高度近视(>600) "
            }else if(historyDisease[0]['right_diopter']==='4'){
              jwsStr = jwsStr + "右眼:远视 "
            }
          }

          if(historyDisease[0]['pregnant_weeks']){
            let weeks = historyDisease[0]['pregnant_weeks'];
            jwsStr = jwsStr + "妊娠"+weeks+'周 ';
          }

          if(historyDisease[0]['other']){
            let other = historyDisease[0]['other'];
            jwsStr = jwsStr + "其他:"+other+' ';
          }
        }
        jwsStr = jwsStr + "出生孕周:"+ this.patient_info['birth_weeks']+'周'+this.patient_info['birth_days']+'天'+' ';
        jwsStr = jwsStr + "矫正孕周:"+ this.patient_info['refine_weeks']+'周'+this.patient_info['refine_days']+'天'+' ';
        jwsStr = jwsStr + "出生体重:"+ this.patient_info['birth_weight']+'克';

        this.jwsDisplayInfo = jwsStr;
      }

    },
      /*离开诊断界面时并没有进行诊断，则将click_time,click_doctor清除*/

    mounted() {
      this.getConclusion(); /*获取诊断热词*/
      this.getDescription();/*获取描述热词*/
      this.determine_report_type(); /*决定按钮类型*/
      this.hospital_name = localStorage.getItem('final_hospital_name');
      this.patient_info = JSON.parse(localStorage.getItem('patient_info'));
      let last_record = this.patient_info['diagnose'].length - 1;
      this.AIdata = this.patient_info['diagnose'][last_record];
      this.AiDiagnose(this.patient_info['diagnose'][last_record]); /*添加诊断AI诊断意见*/
      this.display_history_diseases(); /*展示既往史信息*/
      /*获取照片信息，取出最近一次就诊的眼底照片数据*/
      try {
        let picture_info = this.patient_info.picture;
        console.log("所有照片信息",picture_info);
        let latest = picture_info.length - 1;
        let leftEyeList = [];
        let rightEyeList = [];
        let latestRecord = picture_info[latest].own_to;
        console.log('latest',latestRecord);
        for (let i=0;i<picture_info.length;i++){
          console.log(picture_info[i]['pic_name']);
          if(picture_info[i].own_to !==latestRecord){
            continue
          }else{
            // 将infrared(红外图)照片进行过滤
            if(picture_info[i]['pic_name'].search('infrared') !==-1){
              console.log('i find infrared and filter it');
              continue
            }
            if (picture_info[i]['pic_name'].search('Left') !==-1){
              /*拼接出照片在服务器里面的位置*/
              //let left_src = host+'media/'+picture_info[i]['hospital']+'/'+picture_info[i]['pic_name'];
              let left_src = host+'media/patient_img/'+picture_info[i]['pic_name'];
              leftEyeList.push(left_src);
              console.log('照片在服务器的位置',left_src)
            }else{
              let right_src = host+'media/patient_img/'+picture_info[i]['pic_name'];
              rightEyeList.push(right_src);
            }
          }
        }

        /*倒序照片list 使得后传的照片先展示*/
        leftEyeList = leftEyeList.reverse();
        rightEyeList = rightEyeList.reverse();

        /*如果照片不存在*/
        if(rightEyeList.length===0){
          console.log('左眼没有照片');
          let right_srcList=[];
          right_srcList[0]=host+'media/image/'+'noimg.jpg';
          console.log(right_srcList);
          this.right_src=right_srcList[0];
          this.right_srcList=[];
          this.textarea1 = '无照片';
        }else{
          this.right_src=rightEyeList[0];
          this.right_srcList=rightEyeList;
        }

        if(leftEyeList.length===0){
          console.log('右眼没有照片');
          let left_srcList=[];
          left_srcList[0]=host+'media/image/'+'noimg.jpg';
          console.log(left_srcList);
          this.left_src=left_srcList[0];
          this.left_srcList=[];
          this.textarea2 = '无照片';
        }else{
          this.left_src=leftEyeList[0];
          this.left_srcList=leftEyeList;
        }
      }catch (e) {
        console.log(e)
      }
    },

    beforeDestroy() {
      let patient_id = this.patient_info['patient_id'];
      let superior_hospital = localStorage.getItem('superior_hospital');
      let user_id = localStorage.getItem('user_id');
      let para1 = {};
      let para2 = {};
      let para3 = {};
      para1['patient_id'] = patient_id;
      para2['id'] = superior_hospital;
      para3['id'] = user_id;
      leaveWithoutDiagnose(para1);  /*离开该组件时需要将click_time和click_doctor清除*/

    },

  };

</script>

